<template>
    <div class="row">
        <div class="card profile-card">
            <div class="background" :style="style"></div>
            <Avatar class="profile"/>
            <div class="card-content">
                <h1>{{fullname}}</h1>
                <h2>{{job}}</h2>
                <div class="icon-block">
                    <a :href="linkedin" target="_blank" title="My Linkedin page" rel="noreferrer"><font-awesome-icon :icon="{ prefix: 'fab', iconName: 'linkedin-in' }"/></a>
                    <a :href="facebook" target="_blank" title="My Facebook page" rel="noreferrer"><font-awesome-icon :icon="{ prefix: 'fab', iconName: 'facebook-f' }"/></a>
                    <a :href="github" target="_blank" title="My Github page" rel="noreferrer"><font-awesome-icon :icon="{ prefix: 'fab', iconName: 'github' }"/></a>
                    <a href="mailto:nicolasambroise@hotmail.com" name="contact" title="Send me an email" ><font-awesome-icon icon="envelope"/></a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Avatar from '../Avatar/Avatar'

export default {
    name: 'Card',
    props: {
        fullname : String,
        job : String,
        linkedin : String,
        facebook : String,
        github : String,
        background : String,
    },
    computed: {
      style () {
        return 'background-image: url("' + this.background + '")';
      }
    },
    components: {
       Avatar,
    },
  };
</script>
<style scoped>
.row{
    justify-content: center;
}
.profile-card {
  font-family: 'Open Sans', Arial, sans-serif;
  position: relative;
  float: left;
  overflow: hidden;
  width: 100%;
  color: #ffffff;
  text-align: center;
  height:400px;
  border:none;
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.8);
  max-width: 350px;
}
.profile-card .background {
  width:100%;
  height:100%;
  vertical-align: top;
  opacity: 0.9;
  -webkit-filter: blur(5px) brightness(80%);
  filter: blur(5px) brightness(80%);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.profile-card .card-content {
  width: 100%;
  padding: 15px 25px;
  position: absolute;
  left: 0;
  top: 50%;
}
.profile-card .profile {
  border-radius: 50%;
  position: absolute;
  bottom: 60%;
  left: 50%;
  max-width: 100px;
  opacity: 1;
  box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(255, 255, 255, 1);
  -webkit-transform: translate(-50%, 0%);
  transform: translate(-50%, 0%);
  z-index:10;
  overflow: hidden;
  background-color: #8ec4c9;
}
.profile-card h1 {
  margin: 0 0 5px;
  font-weight: 600;
  font-size:25px;
}
.profile-card h2 {
  display: block;
  font-size: 18px;
  margin-top:10px;
}
.profile-card svg {
  display: inline-block;
    font-size: 16px;
    color: #fff;
    text-align: center;
    border: 1px solid #fff;
    width: 35px;
    height: 35px;
    line-height: 35px;
    padding: 5px;
    border-radius: 50%;
    margin:0 5px;
}
.profile-card .icon-block{
    float:left;
    width:100%;
    margin-top:15px;
}
.profile-card .icon-block a{
    text-decoration:none;
}
.profile-card svg:hover {
  background-color:#fff;
  color:#232323;
  border-color:#232323;
  text-decoration:none;
}

</style>