<template>
  <div id="app" :class="selectedTheme">
    <canvas id="canvas" ref="canvas"></canvas>
    <Header />
    <router-view v-bind:theme="outputTheme"/>
    <Sidebar id="sidebar" v-model="selectedTheme" @input="themeChanged"/>
    <!--<div>App.vue : {{selectedTheme}}</div>-->
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/Header/Header';
import Sidebar from '@/components/Sidebar/Sidebar';
import Footer from '@/components/Footer/Footer';

export default {
  name: 'App',
  components: {
    Header,
    Sidebar,
    Footer,
  },
  props: {
    selectedTheme: {type : String, default: 'theme_light'}
  },
  data: function () {
    return {
      canvas : null,
      context : null,
      cols : 0,
      rows : 0,
      colors : [252, 251, 249, 248, 241, 240],
      triangleColors : [],
      destColors : [],
      outputTheme : 'theme_light'
    }
  },
  methods: {
      init: function(){
          //console.log(document.getElementById('canvas'));
          //console.log(this.$refs);
          this.canvas = this.$refs.canvas;
          //console.log(this.canvas);

          this.context = this.canvas.getContext('2d');
          this.cols = Math.floor(document.body.clientWidth / 24);
          this.rows = Math.floor(document.body.clientHeight / 24) + 1;
          
          this.canvas.width = document.body.clientWidth;
          this.canvas.height = document.body.clientHeight;

          this.drawBackground();
          this.animate();
        },        
        drawTriangle: function(x, y, color, inverted){
          inverted = inverted == undefined ? false : inverted;
      
          this.context.beginPath();
          this.context.moveTo(x, y);
          this.context.lineTo(inverted ? x - 22 : x + 22, y + 11);
          this.context.lineTo(x, y + 22);
          this.context.fillStyle = "rgb("+color+","+color+","+color+")";
          this.context.fill();
          this.context.closePath();
        },        
        getColor: function(){    
          return this.colors[(Math.floor(Math.random() * 6))];
        },        
        drawBackground: function(){
          var eq = null;
          var x = this.cols;
          var destY = 0;
          var y;
          
          while(x--){
            eq = x % 2;
            y = this.rows;
      
            while(y--){
              destY = Math.round((y-0.5) * 24);
      
              this.drawTriangle(x * 24 + 2, eq == 1 ? destY : y * 24, this.getColor());
              this.drawTriangle(x * 24, eq == 1 ? destY  : y * 24, this.getColor(), true);
            }
          }
        },        
        animate: function(){
          var me = this;
          var x = Math.floor(Math.random() * this.cols);
          var y = Math.floor(Math.random() * this.rows);
          var eq = x % 2;
      
          if (eq == 1) {
            me.drawTriangle(x * 24, Math.round((y-0.5) * 24) , this.getColor(), true);
          } else {
            me.drawTriangle(x * 24 + 2, y * 24, this.getColor());
          }
      
          setTimeout(function(){    
            me.animate.call(me);
          }, 10);
        },
       themeChanged() {
                console.log("Theme changed. Selected : ", this.selectedTheme);
                this.outputTheme = this.selectedTheme;
        }
  },
  mounted() {
      this.init();          
  }
};
</script>

<style scoped>
canvas{    
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height:auto;
    z-index: -1;}
</style>