<template>
    <b-sidebar id="sidebar-nia" title="Menu" 
      backdrop
      shadow>
      <div class="px-3 py-2">
        <p>
          App made Bootstrap VueJs.<br>
          More content will come soon !  
        </p>
       
         
        <div role="group" class="pt-5">
           <label for="selectTheme"> Select Theme :</label>
          <b-form-select 
              :options="options" 
              id="selectTheme"
              size="sm" 
              class="mt-1 form-control-select"
              @input="changeTheme()"
              v-model="selectedOption">
          
              <template #first>
                <b-form-select-option :value="null" disabled>-- Please select an option --</b-form-select-option>
              </template>
          </b-form-select>
        </div>
      </div>
    </b-sidebar>
</template>

<script>
  export default {
   name: 'Sidebar',
   data: function () {
        return {
            selectedOption: "theme_light",
            options: [{ value: "theme_light", text: 'Light' }, { value: "theme_modern", text: 'Modern' }, { value: "theme_dark", text: 'Dark' },]
        }
    },
    methods: {
        changeTheme: function () {
            console.log("child changed: " + this.selectedOption);
            this.$emit('input', this.selectedOption);
        }
    }
  };
</script>
<style scoped>
.b-sidebar-outer {
    z-index: 1000;
}
</style>