                        <a :href="facebook" target="_blank" title="My Facebook page" rel="noreferrer"><font-awesome-icon :icon="{ prefix: 'fab', iconName: 'facebook-f' }"/></a>
                        <a :href="github" target="_blank" title="My Github page" rel="noreferrer"><font-awesome-icon :icon="{ prefix: 'fab', iconName: 'github' }"/></a>
