</template>
<script>
import Avatar from '../Avatar/Avatar'

export default {
    name: 'Card',
    props: {
        fullname : String,
        job : String,
        linkedin : String,
        facebook : String,
        github : String,
        background : String,
    },
    computed: {
      style () {
        return 'background-image: url("' + this.background + '")';
      }
