import '@babel/polyfill'
import 'core-js/stable'
import 'regenerator-runtime/runtime'
import 'intersection-observer'


import Vue from 'vue'
import App from './App.vue'

// import Bootstrap and BootstrapVue css files:
import { BootstrapVue, BootstrapVueIcons } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

// import Global custom css files:
import './assets/styles/global.css'

// router setup
import router from './router';

// Font Awesome-related initialization
import { library } from '@fortawesome/fontawesome-svg-core'
import { faEnvelope} from '@fortawesome/free-solid-svg-icons'
import { faFacebook, faFacebookF, faLinkedin, faLinkedinIn, faGithub } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

// Add the specific imported icons
library.add(faEnvelope)
library.add(faFacebookF)
library.add(faFacebook)
library.add(faLinkedin)
library.add(faLinkedinIn)
library.add(faGithub)

Vue.config.productionTip = false

// Enable the FontAwesomeIcon component globally
Vue.component('font-awesome-icon', FontAwesomeIcon)

// Install BootstrapVue + Plugins
Vue.use(BootstrapVue)
Vue.use(BootstrapVueIcons)

new Vue({
  el: '#app',
  router,
  render: h => h(App)
});
